import React from 'react'
import {View, Text, StyleSheet, Dimensions,Image, ImageBackground, Button, TouchableOpacity} from 'react-native'
import SAVANNAH from './SAVANNAH.png';import BANANA from './bananas.png'; import APPLE from './apple.png'; import PINEAPPLE from './pineapple.png'; import JACKFRUIT from './jackfruit.png'
import MANGO from './mango.png'
import BACK from './backArrow.png'
import {App} from './App'

const windowWidth = Dimensions.get('window').width;
const windowHeight = Dimensions.get('window').height;

export class Savannah extends React.Component {
	constructor(props){
		super(props);
		this.state = {
			selectedFruit: ' ',
			back: ' '
		};
	}

	choosePINE = () => {
		this.setState({
			selectedFruit: 'Acheievement 1',
		});
	}

	chooseAPPLE = () => {
		this.setState({
			selectedFruit: 'Acheievement 2',
		});
	}

	chooseJACK = () => {
		this.setState({
			selectedFruit: 'Acheievement 3',
		});
	}

	chooseMANGO = () => {
		this.setState({
			selectedFruit: 'Acheievement 4',
		});
	}

	chooseNANA = () => {
		this.setState({
			selectedFruit: 'Acheievement 5',
		});
	}


	goBack = () => {
		this.setState({ 
			back: 'yes'
		});
	}

	render() {
		if (this.state.back == 'yes') {
				return <App/>
			}	

		return (
			<View>

				<View style ={styles.container1}>
					<TouchableOpacity onPress = {this.goBack}>
		 				<ImageBackground source = {BACK} style = {styles.backButton} alt = "" resizeMode = 'contain' />
		 			</TouchableOpacity>
		 		</View>

		 		<Text style = {styles.text1}>{this.state.selectedFruit}</Text>
				
				<View style ={styles.container}>
					<Text style = {styles.text}> SAVANNAH TREE </Text>
	 				<ImageBackground source = {SAVANNAH} style = {styles.tree} alt = "" resizeMode = 'contain'>
		 				
		 				<TouchableOpacity onPress={this.chooseNANA}>
		 					<Image source = {BANANA} style = {styles.ban} alt = "" resizeMode = 'contain' />
		 				</TouchableOpacity>
	 					
	 					<TouchableOpacity onPress = {this.chooseAPPLE}>
	 						<Image source = {APPLE} style = {styles.apple} alt = "" resizeMode = 'contain' />
	 					</TouchableOpacity>

	 					<TouchableOpacity onPress = {this.choosePINE}>
	 						<Image source = {PINEAPPLE} style = {styles.pine} alt = "" resizeMode = 'contain' />
	 					</TouchableOpacity>

	 					<TouchableOpacity onPress = {this.chooseJACK}>
	 						<Image source = {JACKFRUIT} style = {styles.jack} alt = "" resizeMode = 'contain' />
	 					</TouchableOpacity>

	 					<TouchableOpacity onPress = {this.chooseMANGO}>
	 						<Image source = {MANGO} style = {styles.mango} alt = "" resizeMode = 'contain' />
	 					</TouchableOpacity>

	 				</ImageBackground>
				</View>

			</View>
		)
	}
}

const styles = StyleSheet.create({
	container: {
		justifyContent: 'center',
		alignItems: 'center',
	},

	container1: {
		flex:1,
		flexDirection: 'row',
		alignItems: 'center',
    	justifyContent: 'start',
	},

	backButton: {
		width: windowWidth * 0.1,
    	height: windowHeight * 0.1,
	},

	tree: {
   		width: windowWidth * 0.65,
    	height: windowHeight * 0.65,
    },

    ban: {
		width: windowWidth * 0.08,
    	height: windowHeight * 0.08,
    	top: '220px',
    	left: '50%',
	},

	apple: {
		position: 'absolute',
		width: windowWidth * 0.07,
		height: windowHeight * 0.07,
		top: '150px',
		left: '15%',
	},

	pine:{
		position: 'absolute',
		width: windowWidth * 0.1,
		height: windowHeight * 0.1,
		top: '130px',
		right: '5%',
	}, 

	jack:{
		position: 'absolute',
		width: windowWidth * 0.15,
		height: windowHeight * 0.15,
		top: '240px',
		left: '65%',
		transform: [{rotate: '90deg'}],
	}, 

	mango: {
		position: 'absolute',
		width: windowWidth * 0.05,
		height: windowHeight * 0.05,
		top: '130px',
		left: '30%',
	},

	text: {
		padding: 30,
		fontSize: 40,
		textAlign: 'center',
		color: 'green',
	},

	text1: {
		fontSize: 50,
		textAlign: 'center',
		textAligngVertical: 'bottom',
		color: 'aqua'
	}

	
})
