import React from 'react'
import {View, Text, StyleSheet, Dimensions,Image, ImageBackground, Button, TouchableOpacity} from 'react-native'
import BONSAI from './BONSAI.png'
import BACK from './backArrow.png'
import {App} from './App'
import PINEAPPLE from './pineapple.png'
import APPLE from './apple.png'
import JACK from './jackfruit.png'
import MANGO from './mango.png'
import BANANA from './bananas.png'

const windowWidth = Dimensions.get('window').width;
const windowHeight = Dimensions.get('window').height;

export class Bonsai extends React.Component {
	constructor(props){
		super(props);
		this.state = {
			back: ' ',
			selectedFruit: ' ',
			achieve_0: 'Great Job'
		};
	}

	goBack = () => {
		this.setState({ 
			back: 'yes'
		});
	}

	choosePINE = () => {
		this.setState({
			selectedFruit: 'Acheievement 1',
		});

	}

	chooseAPPLE = () => {
		this.setState({
			selectedFruit: 'Acheievement 2',
		});
	}

	chooseJACK = () => {
		this.setState({
			selectedFruit: 'Acheievement 3',
		});
	}

	chooseMANGO = () => {
		this.setState({
			selectedFruit: 'Acheievement 4',
		});
	}

	chooseNANA = () => {
		this.setState({
			selectedFruit: 'Acheievement 5',
		});
	}	

	render() {
		if (this.state.back == 'yes') {
				return <App/>
			}	

		return (
			<View>

				<View style ={styles.container1}>
					<TouchableOpacity onPress = {this.goBack}>
		 				<ImageBackground source = {BACK} style = {styles.backButton} alt = "" resizeMode = 'contain' />
		 			</TouchableOpacity>
		 		</View>

		 		<Text style = {styles.text1}>{this.state.selectedFruit}</Text>

				<View style ={styles.container}>
					<Text style = {styles.text}> BONSAI TREE </Text>

	 				<ImageBackground source = {BONSAI} style = {styles.tree} alt = "" resizeMode = 'contain'>
						<TouchableOpacity onPress={this.choosePINE}>
							<Image source = {PINEAPPLE} style = {styles.pineapple} alt = "" resizeMode = 'contain'/>
						</TouchableOpacity>

						<TouchableOpacity onPress={this.chooseAPPLE}>
							<Image source = {APPLE} style = {styles.apple} alt = "" resizeMode = 'contain'/>
						</TouchableOpacity>

						<TouchableOpacity onPress = {this.chooseJACK}>
							<Image source = {JACK} style = {styles.jackfruit} alt = "" resizeMode = 'contain'/>
						</TouchableOpacity>

						<TouchableOpacity onPress = {this.chooseMANGO}>
							<Image source = {MANGO} style = {styles.mango} alt = "" resizeMode = 'contain'/>
						</TouchableOpacity>

						<TouchableOpacity onPress = {this.chooseNANA}>
							<Image source = {BANANA} style = {styles.banana} alt = "" resizeMode = 'contain'/>
						</TouchableOpacity>
	 				</ImageBackground>
				</View>

			</View>
		)
	}
}

const styles = StyleSheet.create({
	container: {
		justifyContent: 'center',
		alignItems: 'center',
	},

	container1: {
		flex:1,
		flexDirection: 'row',
		alignItems: 'center',
    	justifyContent: 'start',
	},

	backButton: {
		width: windowWidth * 0.1,
    	height: windowHeight * 0.1,
	},

	pineapple: {
		position: 'absolute',
		width: windowWidth * 0.1,
		height: windowHeight * 0.1,
		top: '200px',
		left: '200px',
	},
	apple: {
		position: 'absolute',
		width: windowWidth * 0.06,
		height: windowHeight * 0.06,
		top: '280px',
		left: '160px',
	},

	jackfruit: {
		position: 'absolute',
		width: windowWidth * 0.1,
		height: windowHeight * 0.1,
		top:'330px',
		left: '345px',
		transform: ([{rotate: '90deg'}]),
	},
	mango: {
		position: 'absolute',
		width: windowWidth * 0.06,
		height: windowHeight * 0.05,
		top: '240px ',
		left: '280px',
	},
	banana: {
		position: 'absolute',
		width: windowWidth * 0.07,
		height: windowHeight * 0.07,
		top:'260px',
		left: '330px',
	},

	tree: {
   		width: windowWidth * 0.7,
    	height: windowHeight * 0.7,
	},
	text: {
		padding: 30,
		fontSize: 40,
		textAlign: 'center',
		color: 'green',
	},
	text1: {
	fontSize: 50,
	textAlign: 'center',
	textAligngVertical: 'bottom',
	color: 'aqua'
}

	
})
