//choose tree screen

import React, { Component } from 'react';
import {
  Image,
  StyleSheet,
  Text,
  View,
  Platform,
  TouchableHighlight,
  TouchableOpacity,
  TouchableWithoutFeedback,
  Animated,
  Easing,
  ImageBackground,
  Alert,
  Dimensions,
  Button,
} from 'react-native';

import OAK from './OAK.png'; import PALM from './PALM.png'; import SAV from './SAVANNAH.png'; import BONSAI from './BONSAI.png'; 
import BAOBAB from './BAOBAB.png'; import DRAG from './DRAGONBLOOD.png'
import {Oak} from './Oak'
import {Baobab} from './baobab'
import {Palm} from './palm'
import {Savannah} from './savannah'
import {Bonsai} from './bonsai'
import {Dragonblood} from './dragonblood'

const windowWidth = Dimensions.get('window').width;
const windowHeight = Dimensions.get('window').height;

export class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      selectedTree:'  '
    };
  }

  chooseOAK = () =>{
    this.setState({ 
      selectedTree: 'oak'
    });
  }

  choosePALM = () =>{
    this.setState({ 
        selectedTree: 'palm'
    });
  }

  chooseSAV = () =>{
    this.setState({ 
      selectedTree: 'savannah'
    });
  }

  chooseBON = () =>{
    this.setState({ 
      selectedTree: 'bonsai'
    });
  }

  chooseBAO = () =>{
    this.setState({ 
      selectedTree: 'baobab'
    });
  }

  chooseDRAG = () =>{
    this.setState({ 
      selectedTree: 'dragonblood'
    });
  }

  render() {
    
    if (this.state.selectedTree == 'oak'){
        return <Oak />
      } else if (this.state.selectedTree == 'baobab'){
        return <Baobab/>
      } else if (this.state.selectedTree == 'palm'){
        return <Palm/>
      } else if (this.state.selectedTree == 'savannah'){
        return <Savannah/>
      } else if (this.state.selectedTree == 'bonsai'){
        return <Bonsai/>
      } else if (this.state.selectedTree == 'dragonblood'){
        return <Dragonblood/>
      }

    return (
      <View style={styles.container}>
        <Text style= {styles.title}> Pick a Tree of Knowledge! </Text>
        
        <Text style =  {styles.title}> {this.state.selectedTree} </Text>  

        <View style={styles.treeRows}>
          <TouchableOpacity onPress = {this.chooseOAK}>
            <ImageBackground source = {OAK} style = {styles.tree} alt = "" resizeMode='contain' />
          </TouchableOpacity>
          <TouchableOpacity onPress = {this.choosePALM}>
             <ImageBackground source = {PALM} style = {styles.tree} alt = "" resizeMode='contain' />
          </TouchableOpacity>
          <TouchableOpacity onPress = {this.chooseSAV}>
            <ImageBackground source = {SAV} style = {styles.tree} alt = "" resizeMode='contain' />
          </TouchableOpacity>          
        </View>

        <View style={styles.treeRows}>
          <TouchableOpacity onPress = {this.chooseBON}>
            <ImageBackground source = {BONSAI} style = {styles.tree} alt = "" resizeMode='contain'  />
          </TouchableOpacity>
          <TouchableOpacity onPress = {this.chooseBAO}>
             <ImageBackground source = {BAOBAB} style = {styles.tree} alt = "" resizeMode='contain'  />
          </TouchableOpacity>
          <TouchableOpacity onPress = {this.chooseDRAG}>
            <ImageBackground source = {DRAG} style = {styles.tree} alt = "" resizeMode='contain' />
          </TouchableOpacity>          
        </View>    

      </View>
    );
  }
}

const styles = StyleSheet.create({
  treeRows:{
    flex: 1, 
    flexDirection: 'row', 
    justifyContent: 'center',
  },

  title: {
    display: 'block',
    marginLeft: 'auto', 
    marginRight: 'auto',
    marginTop: 50,
    marginBottom: 25,
    fontWeight: 'bold',
    fontSize: 30,
    textAlign: 'center',
    color: 'green',
  },

  tree: {
    width: windowWidth * 0.3,
    height: windowHeight * 0.3,
  },

});


let hotWrapper = () => () => App;
if (Platform.OS === 'web') {
  const { hot } = require('react-hot-loader');
  hotWrapper = hot;
}
export default hotWrapper(module)(App);
